<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n    &lt;v-form direction=\&quot;horizontal\&quot; :model=\&quot;ruleForm\&quot; :rules=\&quot;rules\&quot; ref=\&quot;ruleForm\&quot;&gt;\n        &lt;v-form-item label=\&quot;\&quot; :label-col=\&quot;labelCol\&quot; :wrapper-col=\&quot;wrapperCol\&quot; prop=\&quot;name\&quot; has-feedback&gt;\n            &lt;v-input size=\&quot;large\&quot; v-model=\&quot;ruleForm.name\&quot;&gt;&lt;/v-input&gt;\n        &lt;/v-form-item&gt;\n        &lt;v-form-item label=\&quot;\&quot; :label-col=\&quot;labelCol\&quot; :wrapper-col=\&quot;wrapperCol\&quot; prop=\&quot;region\&quot;&gt;\n            &lt;v-select v-model=\&quot;ruleForm.region\&quot; placeholder=\&quot;\&quot; notfound=\&quot;\&quot; :data=\&quot;[{value: '1', label: '1'}, {value: '2', label: '2'}]\&quot;&gt;&lt;/v-select&gt;\n        &lt;/v-form-item&gt;\n        &lt;v-form-item label=\&quot;\&quot; :label-col=\&quot;labelCol\&quot; :wrapper-col=\&quot;wrapperCol\&quot; prop=\&quot;date\&quot;&gt;\n            &lt;v-date-picker v-model=\&quot;ruleForm.date\&quot;&gt;&lt;/v-date-picker&gt;\n        &lt;/v-form-item&gt;\n        &lt;v-form-item label=\&quot;\&quot; :label-col=\&quot;labelCol\&quot; :wrapper-col=\&quot;wrapperCol\&quot;&gt;\n            &lt;v-switch v-model=\&quot;ruleForm.delivery\&quot;&gt;&lt;/v-switch&gt;\n        &lt;/v-form-item&gt;\n        &lt;v-form-item label=\&quot;\&quot; :label-col=\&quot;labelCol\&quot; :wrapper-col=\&quot;wrapperCol\&quot; prop=\&quot;type\&quot;&gt;\n            &lt;v-checkbox-group v-model=\&quot;ruleForm.type\&quot; :data=\&quot;checkboxOpt\&quot;&gt;&lt;/v-checkbox-group&gt;\n        &lt;/v-form-item&gt;\n        &lt;v-form-item label=\&quot;\&quot; :label-col=\&quot;labelCol\&quot; :wrapper-col=\&quot;wrapperCol\&quot; prop=\&quot;resource\&quot;&gt;\n            &lt;v-radio-group v-model=\&quot;ruleForm.resource\&quot; :data=\&quot;[{value: '1', text: ''},{value: '2', text: ''}]\&quot;&gt;&lt;/v-radio-group&gt;\n        &lt;/v-form-item&gt;\n        &lt;v-form-item label=\&quot;\&quot; :label-col=\&quot;labelCol\&quot; :wrapper-col=\&quot;wrapperCol\&quot; prop=\&quot;desc\&quot;&gt;\n            &lt;v-input v-model=\&quot;ruleForm.desc\&quot; type=\&quot;textarea\&quot;&gt;&lt;/v-input&gt;\n        &lt;/v-form-item&gt;\n        &lt;v-form-item :wrapper-col=\&quot;{offset:6, span: 14 }\&quot;&gt;\n            &lt;v-button type=\&quot;primary\&quot; style=\&quot;margin-right:10px\&quot; @click.prevent=\&quot;submitForm('ruleForm')\&quot;&gt;&lt;/v-button&gt;\n            &lt;v-button type=\&quot;ghost\&quot; @click.prevent=\&quot;resetForm('ruleForm')\&quot;&gt;&lt;/v-button&gt;\n        &lt;/v-form-item&gt;\n    &lt;/v-form&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n    data: ()=&gt; ({\n        ruleForm: {\n            name: '',\n            region: '',\n            date: '',\n            delivery: false,\n            type: [],\n            resource: '',\n            desc: ''\n        },\n        rules: {\n            name: [{\n                required: true,\n                message: ''\n            }],\n            region: [{\n                required: true,\n                message: ''\n            }],\n            date: [{\n                required: true,\n                message: ''\n            }],\n            type: [{\n                type: 'array',\n                required: true,\n                message: ''\n            }],\n            resource: [{\n                required: true,\n                message: ''\n            }],\n            desc: [{\n                required: true,\n                message: ''\n            }]\n        },\n        checkboxOpt: [{\n                label: '/',\n                value: '1'\n            },\n            {\n                label: '',\n                value: '2'\n            },\n            {\n                label: '',\n                value: '3'\n            },\n            {\n                label: '',\n                value: '4'\n            }\n        ],\n        labelCol: {\n            span: 6\n        },\n        wrapperCol: {\n            span: 14\n        }\n    }),\n    methods: {\n        submitForm(formName) {\n            this.$refs[formName].validate((valid) =&gt; {\n                if (valid) {\n                    alert('submit!');\n                } else {\n                    console.log('error submit!!');\n                    return false;\n                }\n            });\n        },\n        resetForm(formName) {\n            this.$refs[formName].resetFields();\n        }\n    }\n}\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
    <v-form direction="horizontal" :model="ruleForm" :rules="rules" ref="ruleForm">
        <v-form-item label="" :label-col="labelCol" :wrapper-col="wrapperCol" prop="name" has-feedback="">
            <v-input size="large" v-model="ruleForm.name"></v-input>
        </v-form-item>
        <v-form-item label="" :label-col="labelCol" :wrapper-col="wrapperCol" prop="region">
            <v-select v-model="ruleForm.region" placeholder="" notfound="" :data="[{value: '1', label: '1'}, {value: '2', label: '2'}]"></v-select>
        </v-form-item>
        <v-form-item label="" :label-col="labelCol" :wrapper-col="wrapperCol" prop="date">
            <v-date-picker v-model="ruleForm.date"></v-date-picker>
        </v-form-item>
        <v-form-item label="" :label-col="labelCol" :wrapper-col="wrapperCol">
            <v-switch v-model="ruleForm.delivery"></v-switch>
        </v-form-item>
        <v-form-item label="" :label-col="labelCol" :wrapper-col="wrapperCol" prop="type">
            <v-checkbox-group v-model="ruleForm.type" :data="checkboxOpt"></v-checkbox-group>
        </v-form-item>
        <v-form-item label="" :label-col="labelCol" :wrapper-col="wrapperCol" prop="resource">
            <v-radio-group v-model="ruleForm.resource" :data="[{value: '1', text: ''},{value: '2', text: ''}]"></v-radio-group>
        </v-form-item>
        <v-form-item label="" :label-col="labelCol" :wrapper-col="wrapperCol" prop="desc">
            <v-input v-model="ruleForm.desc" type="textarea"></v-input>
        </v-form-item>
        <v-form-item :wrapper-col="{offset:6, span: 14 }">
            <v-button type="primary" style="margin-right:10px" @click.prevent="submitForm('ruleForm')"></v-button>
            <v-button type="ghost" @click.prevent="resetForm('ruleForm')"></v-button>
        </v-form-item>
    </v-form>
</template>

</div>
