<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n    &lt;v-form direction=\&quot;horizontal\&quot; :model=\&quot;customForm\&quot; :rules=\&quot;customRules\&quot; ref=\&quot;customRuleForm\&quot;&gt;\n        &lt;v-form-item label=\&quot;\&quot; :label-col=\&quot;labelCol\&quot; :wrapper-col=\&quot;wrapperCol\&quot; prop=\&quot;pass\&quot; has-feedback&gt;\n            &lt;v-input type=\&quot;password\&quot; size=\&quot;large\&quot; v-model=\&quot;customForm.pass\&quot;&gt;&lt;/v-input&gt;\n        &lt;/v-form-item&gt;\n        &lt;v-form-item label=\&quot;\&quot; :label-col=\&quot;labelCol\&quot; :wrapper-col=\&quot;wrapperCol\&quot; prop=\&quot;checkPass\&quot; has-feedback&gt;\n            &lt;v-input type=\&quot;password\&quot; size=\&quot;large\&quot; v-model=\&quot;customForm.checkPass\&quot;&gt;&lt;/v-input&gt;\n        &lt;/v-form-item&gt;\n        &lt;v-form-item label=\&quot;\&quot; :label-col=\&quot;labelCol\&quot; :wrapper-col=\&quot;wrapperCol\&quot; prop=\&quot;age\&quot; has-feedback&gt;\n            &lt;v-input size=\&quot;large\&quot; v-model=\&quot;customForm.age\&quot;&gt;&lt;/v-input&gt;\n        &lt;/v-form-item&gt;\n        &lt;v-form-item :wrapper-col=\&quot;{offset:6, span: 14 }\&quot;&gt;\n            &lt;v-button type=\&quot;primary\&quot; style=\&quot;margin-right:10px\&quot; @click.prevent=\&quot;submitForm('customRuleForm')\&quot;&gt;&lt;/v-button&gt;\n            &lt;v-button type=\&quot;ghost\&quot; @click.prevent=\&quot;resetForm('customRuleForm')\&quot;&gt;&lt;/v-button&gt;\n        &lt;/v-form-item&gt;\n    &lt;/v-form&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n    data() {\n        var checkAge = (rule, value, callback) =&gt; {\n            var age = parseInt(value, 10);\n\n            setTimeout(() =&gt; {\n                if (!Number.isInteger(age)) {\n                    callback(new Error(''));\n                } else {\n                    if (age &lt; 18) {\n                        callback(new Error('18'));\n                    } else {\n                        callback();\n                    }\n                }\n            }, 1000);\n        };\n        var validatePass = (rule, value, callback) =&gt; {\n            if (value === '') {\n                callback(new Error(''));\n            } else {\n                if (this.customForm.checkPass !== '') {\n                    this.$refs.customRuleForm.validateField('checkPass');\n                }\n                callback();\n            }\n        };\n        var validatePass2 = (rule, value, callback) =&gt; {\n            if (value === '') {\n                callback(new Error(''));\n            } else if (value !== this.customForm.pass) {\n                callback(new Error('!'));\n            } else {\n                callback();\n            }\n        };\n        return {\n            customForm: {\n                pass: '',\n                checkPass: '',\n                age: ''\n            },\n            customRules: {\n                pass: [{\n                        required: true,\n                        message: ''\n                    },\n                    {\n                        validator: validatePass\n                    }\n                ],\n                checkPass: [{\n                        required: true,\n                        message: ''\n                    },\n                    {\n                        validator: validatePass2\n                    }\n                ],\n                age: [{\n                        required: true,\n                        message: ''\n                    },\n                    {\n                        validator: checkAge\n                    }\n                ]\n            },\n            labelCol: {\n                span: 6\n            },\n            wrapperCol: {\n                span: 14\n            }\n        } \n    },\n    methods: {\n        submitForm(formName) {\n            this.$refs[formName].validate((valid) =&gt; {\n            if (valid) {\n                alert('submit!');\n            } else {\n                console.log('error submit!!');\n                return false;\n            }\n            });\n        },\n        resetForm(formName) {\n            this.$refs[formName].resetFields();\n        }\n    }\n}\n&quot;,&quot;style&quot;:null}">
