<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n    &lt;v-form direction=\&quot;horizontal\&quot; :model=\&quot;dynamicValidateForm\&quot; ref=\&quot;dynamicValidateForm\&quot;&gt;\n        &lt;v-form-item label=\&quot;\&quot; :label-col=\&quot;labelCol\&quot; :wrapper-col=\&quot;wrapperCol\&quot; prop=\&quot;email\&quot; :rules=\&quot;[\n                { required: true, message: '', trigger: 'blur' },\n                { type: 'email', message: '', trigger: 'blur,change' }\n            ]\&quot;&gt;\n            &lt;v-input size=\&quot;large\&quot; v-model=\&quot;dynamicValidateForm.email\&quot;&gt;&lt;/v-input&gt;\n        &lt;/v-form-item&gt;\n        &lt;v-form-item :label-col=\&quot;labelCol\&quot; :wrapper-col=\&quot;wrapperCol\&quot; v-for=\&quot;(domain, index) in dynamicValidateForm.domains\&quot; :label=\&quot;'' + index\&quot; :key=\&quot;domain.key\&quot; :prop=\&quot;'domains.' + index + '.value'\&quot; :rules=\&quot;{\n                required: true, message: '', trigger: 'blur'\n            }\&quot;&gt;\n            &lt;v-input size=\&quot;large\&quot; v-model=\&quot;domain.value\&quot; style=\&quot;width:78%;margin-right:10px\&quot;&gt;&lt;/v-input&gt;&lt;v-button @click.prevent=\&quot;removeDomain(domain)\&quot;&gt;&lt;/v-button&gt;\n        &lt;/v-form-item&gt;\n        &lt;v-form-item :wrapper-col=\&quot;{offset:6, span: 14 }\&quot;&gt;\n            &lt;v-button type=\&quot;primary\&quot; style=\&quot;margin-right:10px\&quot; @click.prevent=\&quot;submitForm('dynamicValidateForm')\&quot;&gt;&lt;/v-button&gt;\n            &lt;v-button @click=\&quot;addDomain\&quot;&gt;&lt;/v-button&gt;\n            &lt;v-button @click=\&quot;resetForm('dynamicValidateForm')\&quot;&gt;&lt;/v-button&gt;\n        &lt;/v-form-item&gt;\n    &lt;/v-form&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n    data() {\n        return {\n            dynamicValidateForm: {\n                domains: [{\n                    value: ''\n                }],\n                email: ''\n            },\n            labelCol: {\n                span: 6\n            },\n            wrapperCol: {\n                span: 14\n            }\n        } \n    },\n    methods: {\n        submitForm(formName) {\n            this.$refs[formName].validate((valid) =&gt; {\n            if (valid) {\n                alert('submit!');\n            } else {\n                console.log('error submit!!');\n                return false;\n            }\n            });\n        },\n        resetForm(formName) {\n            this.$refs[formName].resetFields();\n        },\n        removeDomain(item) {\n            var index = this.dynamicValidateForm.domains.indexOf(item)\n            if (index !== -1) {\n            this.dynamicValidateForm.domains.splice(index, 1)\n            }\n        },\n        addDomain() {\n            this.dynamicValidateForm.domains.push({\n                value: '',\n                key: Date.now()\n            });\n        }\n    }\n}\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
    <v-form direction="horizontal" :model="dynamicValidateForm" ref="dynamicValidateForm">
        <v-form-item label="" :label-col="labelCol" :wrapper-col="wrapperCol" prop="email" :rules="[
                { required: true, message: '', trigger: 'blur' },
                { type: 'email', message: '', trigger: 'blur,change' }
            ]">
            <v-input size="large" v-model="dynamicValidateForm.email"></v-input>
        </v-form-item>
        <v-form-item :label-col="labelCol" :wrapper-col="wrapperCol" v-for="(domain, index) in dynamicValidateForm.domains" :label="'' + index" :key="domain.key" :prop="'domains.' + index + '.value'" :rules="{
                required: true, message: '', trigger: 'blur'
            }">
            <v-input size="large" v-model="domain.value" style="width:78%;margin-right:10px"></v-input><v-button @click.prevent="removeDomain(domain)"></v-button>
        </v-form-item>
        <v-form-item :wrapper-col="{offset:6, span: 14 }">
            <v-button type="primary" style="margin-right:10px" @click.prevent="submitForm('dynamicValidateForm')"></v-button>
            <v-button @click="addDomain"></v-button>
            <v-button @click="resetForm('dynamicValidateForm')"></v-button>
        </v-form-item>
    </v-form>
</template>

</div>
