<style>
.box-demo-show .ant-form-horizontal{
    max-width: 540px;
}
</style>
<script>
export default {
    data() {
        var checkAge = (rule, value, callback) => {
            var age = parseInt(value, 10);

            setTimeout(() => {
                if (!Number.isInteger(age)) {
                    callback(new Error(''));
                } else {
                    if (age < 18) {
                        callback(new Error('18'));
                    } else {
                        callback();
                    }
                }
            }, 1000);
        };
        var validatePass = (rule, value, callback) => {
            if (value === '') {
                callback(new Error(''));
            } else {
                if (this.customForm.checkPass !== '') {
                    this.$refs.customRuleForm.validateField('checkPass');
                }
                callback();
            }
        };
        var validatePass2 = (rule, value, callback) => {
            if (value === '') {
                callback(new Error(''));
            } else if (value !== this.customForm.pass) {
                callback(new Error('!'));
            } else {
                callback();
            }
        };
        return {
            ruleForm: {
                name: '',
                region: '',
                date: '',
                delivery: false,
                type: [],
                resource: '',
                desc: ''
            },
            rules: {
                name: [{
                    required: true,
                    message: ''
                }],
                region: [{
                    required: true,
                    message: ''
                }],
                date: [{
                    required: true,
                    message: ''
                }],
                type: [{
                    type: 'array',
                    required: true,
                    message: ''
                }],
                resource: [{
                    required: true,
                    message: ''
                }],
                desc: [{
                    required: true,
                    message: ''
                }]
            },
            checkboxOpt: [{
                label: '/',
                value: '1'
            },
            {
                label: '',
                value: '2'
            },
            {
                label: '',
                value: '3'
            },
            {
                label: '',
                value: '4'
            }],
            customForm: {
                pass: '',
                checkPass: '',
                age: ''
            },
            customRules: {
                pass: [{
                        required: true,
                        message: ''
                    },
                    {
                        validator: validatePass
                    }
                ],
                checkPass: [{
                        required: true,
                        message: ''
                    },
                    {
                        validator: validatePass2
                    }
                ],
                age: [{
                        required: true,
                        message: ''
                    },
                    {
                        validator: checkAge
                    }
                ]
            },
            dynamicValidateForm: {
                domains: [{
                    value: ''
                }],
                email: ''
            },
            labelCol: {
                span: 6
            },
            wrapperCol: {
                span: 14
            }
        } 
    },
    methods: {
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    alert('submit!');
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        resetForm(formName) {
            this.$refs[formName].resetFields();
        },
        removeDomain(item) {
            var index = this.dynamicValidateForm.domains.indexOf(item)
            if (index !== -1) {
            this.dynamicValidateForm.domains.splice(index, 1)
            }
        },
        addDomain() {
            this.dynamicValidateForm.domains.push({
                value: '',
                key: Date.now()
            });
        }
    }
}
</script>


